:root {
  font-size: 62.5%;
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;
  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

.root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

a {
  font-weight: 500;
  color: #646cff;
  text-decoration: inherit;
}
a:hover {
  color: #535bf2;
}

body {
  margin: 0;
  min-height: 100vh;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 3.8rem;
}

h1, h2, h3 {
  margin-bottom: 3rem;
  line-height: 1.1;
}

p, a {
  font-size: 2rem;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  a:hover {
    color: #747bff;
  }
  button {
    background-color: #f9f9f9;
  }
}
[class$=__container] {
  padding-block: 5rem;
  overflow: hidden;
  height: 100%;
}

[class$=__wrapper] {
  width: 90%;
  max-width: 120rem;
  margin-inline: auto;
  height: 100%;
}

.main {
  text-align: center;
}
.main__text {
  margin-bottom: 3rem;
}
.main__icons {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.main__icon {
  width: 5rem;
}

.header__heading {
  text-align: center;
}

.footer {
  text-align: center;
}